@import url('https://fonts.googleapis.com/css2?family=Jost&display=swap');

.content {
	// figure {
	// 	width: 100%;
	// 	height: 300px;
	// 	background: gray;
	// 	opacity: 0;
	// 	transition: 1s 0.3s;
	// }
	> .inner {
		padding: 100px 10vw;
		min-height: 600px;
		opacity: 0;
		transform: translateY(200px);
		transition-property: opacity, transform;
		transition-duration: 1s;
		transition-delay: 0.5s;

		h1 {
			width: 100%;
			font: normal 70px/1 'jost';
			color: #333;
			margin-bottom: 120px;

			&::before {
				content: '';
				display: block;
				width: 100px;
				height: 1px;
				background: #333;
				margin-bottom: 30px;
			}
		}
	}
}

//활성화 모션
.content.on {
	figure {
		opacity: 1;
	}
	> .inner {
		opacity: 1;
		transform: translateY(0px);
	}
}
@media screen and (max-width: $tablet) {
	.content {
		figure {
			height: 150px;
		}
		> .inner {
			padding: 70px 10vw;

			h1 {
				font: normal 50px/1 'work sans';

				&::before {
					width: 60px;
					margin-bottom: 15px;
				}
			}
		}
	}
}

//mobile
@media screen and (max-width: $mobile) {
	.content {
		figure {
			display: none;
		}
		> .inner {
			padding: 50px 10vw;

			h1 {
				font: normal 30px/1 'work sans';

				&::before {
					width: 30px;
					margin-bottom: 10px;
				}
			}
		}
	}
}
